import { useEffect, useRef } from "react";
import { createChart } from "lightweight-charts";

const DataChart = () => {
  const ws = useRef();
  const chartRef = useRef();
  const lineSeriesRef = useRef();

  const chartOptions = {
    layout: {
      textColor: "black",
      background: { type: "solid", color: "white" },
    },
  };

  useEffect(() => {
    const chart = createChart(chartRef.current, chartOptions);
    lineSeriesRef.current = chart.addLineSeries({
      color: "#2962FF",
    });

    ws.current = new WebSocket("wss://ws.okx.com:8443/ws/v5/public");

    ws.current.onopen = () => {
      console.log("success connection");

      const subscribeMsg = {
        op: "subscribe",
        args: [
          {
            channel: "trades",
            instId: "BTC-USDT",
          },
        ],
      };

      ws.current.send(JSON.stringify(subscribeMsg));
    };

    const formatTimestamp = (ts) => {
      const timestamp = parseInt(ts, 10);
      return Math.floor(timestamp / 1000); // 초 단위로 변환
    };

    ws.current.onmessage = (event) => {
      const message = JSON.parse(event.data);
      // console.log(message);

      if (message.event !== "subscribe" && message.data) {
        message.data.forEach((trade) => {
          // console.log(trade);

          const newData = {
            value: parseFloat(trade.px),
            time: formatTimestamp(trade.ts),
          };
          lineSeriesRef.current.update(newData);
          chart.timeScale().fitContent();
        });
      }
    };

    ws.current.onclose = () => {
      console.log("connection closed");
    };

    ws.current.onerror = (error) => {
      console.error("error: ", error);
    };

    return () => {
      if (ws.current) {
        ws.current.close();
      }

      chart.remove();
    };
  }, []);

  return (
    <div>
      <h2>BTCUSDT 마지막 체결 데이터(실시간)</h2>
      <div
        id="container"
        ref={chartRef}
        style={{ width: "100%", height: "500px" }}
      />
    </div>
  );
};

export default DataChart;
